import { useEffect, useState } from 'mithril-hooks';
import { dialog, notification } from 'dialogic';
let useDialogicCounter = 0;
export const useDialogic = (allProps) => {
    const { isShow, isHide, instance, deps = [], props = {}, } = allProps;
    // Use dialogic id if not set
    const [id] = useState(useDialogicCounter++);
    const augProps = {
        ...props,
        ...(props.dialogic
            ? {
                dialogic: {
                    ...props.dialogic,
                    id: props.dialogic.id || id,
                },
            }
            : {
                dialogic: {
                    id,
                },
            }),
    };
    const showInstance = () => {
        instance.show(augProps);
    };
    const hideInstance = () => {
        instance.hide(augProps);
    };
    useEffect(() => {
        if (isShow !== undefined) {
            if (isShow) {
                showInstance();
            }
            else {
                hideInstance();
            }
        }
    }, [...deps, isShow]);
    useEffect(() => {
        if (isHide !== undefined) {
            if (isHide) {
                hideInstance();
            }
        }
    }, [...deps, isHide]);
    useEffect(() => {
        return () => {
            hideInstance();
        };
    }, []);
    return {
        show: showInstance,
        hide: hideInstance,
    };
};
/**
 * `useDialogic` with `instance` preset to `dialog`.
 */
export const useDialog = (props) => useDialogic({ ...props, instance: dialog });
/**
 * `useDialogic` with `instance` preset to `notification`.
 */
export const useNotification = (props) => useDialogic({ ...props, instance: notification });
